@import url(https://fonts.googleapis.com/css?family=Roboto+Condensed:400,700);
@import url(https://fonts.googleapis.com/css?family=Fira+Sans:400,500,600,700,800);
#brand {
    font-family:Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
    font-size: 25px;
    color: orange;
}

.searchbar *{
    font-size: 20px;
    margin: 5px;
}

.search {
    min-width: 40%;
    margin-left: 15px;
    border-radius: 5px;
}

.search-btn {
    padding: 0%;
    margin-left: 0%;
}
.welcome-messge{
    font-family:Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
    font-size: 20px;
    color: orange;
    font-weight: bold;
    padding:8px;
}
.logout {
    background-color: rgb(34, 34, 25);
    color: white;
    font-size: 18px;
    margin: 8px;
    padding:2px;
    border-style: none;
}
.logout:hover {
    text-decoration: none;
    background-color: rgb(34, 34, 25);
    color: white;
    font-size: 18px;
    margin: 8px;
    padding:2px;
    border-style: none;
}

.cart {
    font-size: 18px;
}
footer {
    background-color: #1A3E4C;
    color:#E4F1F6;
}

.top-footer {
    display: flex;
    justify-content: space-evenly;
    margin-top:15px;
}

.top-heading {
    font-size: 16px;
    font-weight: bold;
    
}

.line {
   border-top: 1px solid #c9c7c7;
}
a {
    color:#E4F1F6;
}
.sliderImage > img {
    height: 400px;
    width: 100%;
}

.home-container{
    margin-top: 30px;
    width: 95%;   
}

.categoryCard {
    border: 2px solid #f8f8ff;
    border-radius: 20px;
    max-width: 30%;
    max-height: 950px;
    margin: 15px;
    background-color: #f8f8ff;
    display: inline-block;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    overflow: hidden;
}

.categoryCard > img {
    height: 180px;
    width: 130px;
    margin: 10px;
}

.homeTitle {
    font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
    font-weight: 600;
    color: #1A3E4C;
} 

.cardTitle {
    font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
    font-weight: 600;
    color: #347B98;
}

.categoryCard * h4{
    color: orange;
    font-family: 'Roboto Condensed', sans-serif;
}

.filterTitle {
    color: #347B98;
    font-weight: 600;
}
.searchContainer {
    border: 2px solid #f8f8ff;
    border-radius: 10px;
    box-shadow: 4px 4px 12px grey;
    background-color: #f8f8ff;
    margin: 20px 0 20px 10px;
    max-width: 95%;
    max-height: 800px;
    overflow: hidden;
}

.loader {
    margin-left: 35%;
}

.searchContainer * {
    font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
}

.searchContainer * img {
    max-width: 80%;
    max-height: 250px;
    margin: 22px
}

.link {
    color: #67AFCB;
}

.link:hover {
    color: purple;
}

.searchContainer * p {
    font-size: 20px;
}

.rating {
    font-weight: 550;
    font-size: 24px;
    color: teal
}

.amount {
    font-size: 24px;
}

.noData {
    font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
    color: red
}
.detailTitle {
    color: #67AFCB;
    font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
}

.datailImage * img {
    max-width: 80%;
    max-height: 400px
}

.price {
    font-weight: 600;
    font-size: 20px;
}

.description {
    font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
}

.description * b {
    font-size: 18px;
}
.reviewHeading {
    font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
    font-weight: 600;
    color: #347B98;
}

.reviews {
    font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
}

.reviews * p {
    font-size: 16px;
}

.reviewTitle {
    color: #67AFCB;
    margin-bottom: 0%;
}

.reviewDate {
    color: grey;
}
.order-outer-container{
  background: url('http://all4desktop.com/data_images/original/4236532-background-images.jpg');
  font-family: 'Roboto Condensed', sans-serif;
  color: #262626;
  margin-top: 0;
  padding-top: 5%;
  padding-bottom: 5%;
  height:auto;
}
.order-container{
  width: 100%;
  padding-right: 15px;
  padding-left: 15px;
  margin-right: auto;
  margin-left: auto;
}
@media (min-width: 1200px)
{
  .order-container{
    max-width: 1140px;
  }
}
.d-flex{
  display: flex;
  flex-direction: row;
  background: #f6f6f6;
  border-radius: 0 0 5px 5px;
  padding: 25px;
}

.order-form{
  flex: 4 1;
}

.Yorder{
  flex: 2 1;
}

.order-title{
  background: linear-gradient(to bottom right, #5195A8 0%, #70EAFF 100%);
  border-radius:5px 5px 0 0 ;
  padding: 20px;
  color: #f6f6f6;
}
.order-h2{
  margin: 0;
  padding-left: 15px; 
}
.required{
  color: red;
}
.order-label, .order-table{
  display: block;
  margin: 20px;
}
.order-label>span{
  float: left;
  width: 25%;
  margin-top: 12px;
  padding-right: 10px;
}
.order-label>input[type="text"], .order-label>input[type="tel"], .order-label>input[type="email"]
{
  width: 70%;
  height: 30px;
  padding: 5px 10px;
  margin-bottom: 10px;
  border: 1px solid #dadada;
  color: #888;
}
/* select{
  width: 72%;
  height: 45px;
  padding: 5px 10px;
  margin-bottom: 10px;
} */
.Yorder{
  margin-top: 15px;
  height: 600px;
  padding: 20px;
  border: 1px solid #dadada;
}
.order-table{
  margin: 0;
  padding: 0;
}
.order-table th{
  border-bottom: 1px solid #dadada;
  padding: 5px 0;
}
.order-table tr>td:nth-child(1){
  text-align: left;
  color: #2d2d2a;
}
.order-table tr>td:nth-child(2){
  text-align: right;
  color: #52ad9c;
}
.order-table td{
  border-bottom: 1px solid #dadada;
  padding: 20px 20px 10px 0;
}

.order-disclaimer{
  display: block;
  color: #888;
  margin: 0;
  padding-left: 25px;
}
.order-label1{
    margin: 5px;
}

/* .Yorder>div{
  padding: 15px 0; 
} */
.radios{
  padding: 2px 0;
  margin-top: 0px;
  margin-bottom: 0px;
}
.order-button{
  width: 100%;
  margin-top: 10px;
  padding: 10px;
  border: none;
  border-radius: 30px;
  background: #52ad9c;
  color: #fff;
  font-size: 15px;
  font-weight: bold;
}
.order-button:hover{
  cursor: pointer;
  background: #428a7d;
}
.error-display{
  color: red;
  font-size: smaller;
  
}

.order-product-name {
  display: inline-block;
  width: 180px;
  white-space: nowrap;
  overflow: hidden !important;
  text-overflow: ellipsis;
}

* {
  box-sizing: border-box;
}
.main-continer {
  background: url('http://all4desktop.com/data_images/original/4236532-background-images.jpg');
  min-height: 100vh;
  display: flex;
  font-weight: 400;
  font-family: 'Fira Sans', sans-serif;
}
.form-class {
  width:50%;
  height: 90%;
  background-color: white;
  box-shadow: 0 4px 8px 0 rgb(228, 216, 196), 0 6px 20px 0 rgb(204, 200, 200);
  margin-left: 20%;
  margin-top: 5%;
  border-radius: 15px;
}
.font-size {
  font-weight: 500;
  font-family: 'Fira Sans', sans-serif;
} 
.form-data{
  margin-left: 10%;
  margin-right: 10%;
  padding: 5%;
}

.error-display{
  color: red;
  font-size: smaller;
  
}
